import {CommonUtils} from "../../../libs/index"

let currentLocation = {
  lng: 0,
  lat: 0,
  provinceName: '',
  cityName: ''
};

function getCurrentPosition() {
  return new Promise((resolve, reject) => {
    try {
      let paramLng = parseFloat(CommonUtils.getUrlParam('lng'));
      let paramLat = parseFloat(CommonUtils.getUrlParam('lat'));
      console.log('paramLng:' + paramLng);
      console.log('paramLat:' + paramLat);
      if (paramLng && paramLat) {
        resolve([paramLng, paramLat]);
        return;
      }
      console.log('resolve([paramLng, paramLat]);');
    } catch (e) {
    }

    AMap.plugin('AMap.Geolocation', () => {
      let geolocation = new AMap.Geolocation({
        enableHighAccuracy: true,//是否使用高精度定位，默认:true
        timeout: 10000,          //超过10秒后停止定位，默认：无穷大
        maximumAge: 1000 * 60,           //定位结果缓存0毫秒，默认：0
        convert: true,           //自动偏移坐标，偏移后的坐标为高德坐标，默认：true
      });

      geolocation.getCurrentPosition((status, result) => {
        console.log('getCurrentPosition:' + status);
        console.log(result);
        if (result.info == 'SUCCESS') {
          let position = result.position;
          resolve([position.lng, position.lat]);
          // resolve(['113.32341671', '23.02831845']);
        } else {
          reject('当前无法获取定位');
        }
      });
    });
  });
}

function getAddress(lng, lat) {
  console.log('getAddress');
  return new Promise((resolve, reject) => {
    AMap.plugin('AMap.Geocoder', function() {
      let geocoder = new AMap.Geocoder();
      geocoder.getAddress([lng, lat], (statusAddress, resultAddress) => {
        console.log('getAddress:' + statusAddress);
        console.log(resultAddress);
        if (statusAddress == 'complete' && resultAddress.info == 'OK') {
          let addressComponent = resultAddress.regeocode.addressComponent;
          resolve([addressComponent.province, addressComponent.city]);
        }
      });
    });
  });
}

export default {
  async requestCurrentPosition() {
    if (currentLocation.lng > 0 && currentLocation.lat > 0) {
      return currentLocation;
    }

    try {
      let [lng, lat] = await getCurrentPosition();
      console.log([lng, lat]);
      let [province, city] = await getAddress(lng, lat);
      console.log([province, city]);
      currentLocation.lng = lng;
      currentLocation.lat = lat;

      currentLocation.provinceName = province;
      currentLocation.cityName = city;
    } catch (e) {
      console.log(e);
      throw e;
    }

    return currentLocation;
  },
  setMapToTarget(container, lng, lat) {
// AMapUI.setDomLibrary($);
    AMapUI.loadUI(['control/BasicControl'], function (BasicControl) {
      // 34.7677670000,113.7306410000
      let map = new AMap.Map(container, {
        zoom: 16,  //设置地图显示的缩放级别
        center: [lng, lat],//设置地图中心点坐标
        viewMode: '2D',  //设置地图模式
        lang: 'zh_cn',  //设置地图语言类型
      });
      let marker = new AMap.Marker({
        position: new AMap.LngLat(lng, lat),   // 经纬度对象，也可以是经纬度构成的一维数组[116.39, 39.9]
        title: '当前位置'
      });

      map.add(marker);
      //缩放控件
      map.addControl(new BasicControl.Zoom({
        position: 'rb', //left top，左上角
        showZoomNum: false //显示zoom值
      }));
    });
  }
}