import {Http, CommonUtils, url} from "../../../libs/index";

let url_prex = url('ali');

function handleData(item) {
  let specialService = item.specialService;
  if (specialService) {
    specialService = item.specialService.split(',');
  } else {
    specialService = [];
  }
  item.specialService = specialService;

  let pointDistance = item.pointDistance;
  if (pointDistance) {
    pointDistance = Math.floor(pointDistance);
    item.pointDistance = pointDistance + "M";
  }
}

export default {
  async getNearbyBusinessHall(lng, lat, cityName, page) {
    try {
      let response = await Http.post(url_prex + 'getNearbyBusinessHall', {
        apiKey: 'ee721c4d2abe8a59a687e16475b061fb',
        userName: 'safeapp',
        coordinate: `${lng},${lat}`,
        // coordinate: `108.318757,22.796992`,
        cityName: cityName,
        channelId: CommonUtils.getFullChannel() || '默认渠道',
        page: page,
        size: 10,
        egg: 1
      });

      if (response.Returncode == 200) {
        let jo = response.jo;
        let total = jo.total;
        let list = jo.hallList;
        if (total&&list.length>0) {
          for (let item of list) {
            handleData(item);
          }
          return {total: total, list: list};
        }else {
          throw '无结果';
        }
      }
    } catch (e) {
      throw e;
    }


  },

  async searchBusinessHallByKeyword(lng, lat,keyword, cityName, page) {
    try {
      let response = await Http.post(url_prex + 'searchBusinessHallByKeyword', {
        apiKey: 'ee721c4d2abe8a59a687e16475b061fb',
        userName: 'safeapp',
        coordinate: `${lng},${lat}`,
        cityName: cityName,
        keyWord: keyword,
        channelId: CommonUtils.getFullChannel() || '默认渠道',
        page: page,
        size: 10
      });

      if (response.Returncode == 200) {
        let jo = response.jo;
        let total = jo.total;
        let list = jo.hallList;
        if (total&&list.length>0) {
          for (let item of list) {
            handleData(item);
          }
          return {total: total, list: list};
        }else {
          throw '无搜索结果';
        }
      }
    } catch (e) {
      throw e;
    }
  }
}