<template>
    <div class="businessHallItem" @click="toDetail">
        <span>111</span>
        <p class="businessHallItem_name"
           v-if="showName&&nameHtml"
           v-html="nameHtml">
        </p>

        <p class="businessHallItem_address"
           v-if="showAddress&&address"
           v-html="addressHtml">
            <!--            {{address}}-->
        </p>

        <p class="businessHallItem_distance"
           v-if="showDistance&&distance">
            {{distance}}
        </p>

        <p class="businessHallItem_time"
           v-if="showTime&&time">
            营业时间：{{time}}
        </p>

        <p class="businessHallItem_telphone"
           v-if="showTelphone&&telphone">
            电话：{{telphone}}
        </p>

        <div class="businessHallItem_scope" v-if="showScopeList&&scopeList.length>0">
            <span class="businessHallItem_scope_title">特殊服务：</span>
            <div class="businessHallItem_scope_list"
                 ref="businessHallItem_scope_list">
                <span v-for="(item,index) in scopeList"
                      :key="index"
                      class="businessHallItem_scope_list_item"
                      :class="{businessHallItem_scope_list_item_key:item==keyWord}"
                      ref="businessHallItem_scope_list_item">
                    {{item}}
                </span>
            </div>
            <div class="businessHallItem_scope_spread" v-if="showSpread" @click.stop="spread">展开</div>
        </div>
        <p style="height: 0.2rem"></p>
    </div>
</template>

<script>
  import {Toast} from 'vant';

  export default {
    name: "BusinessHallItem",
    components: {Toast},
    props: {
      keyWord: '',
      name: '',
      address: '',
      distance: '',
      time: '',
      telphone: '',
      scopeList: {
        type: Array
      },
      lng: 0,
      lat: 0,
      showName: {type: Boolean, default: true},
      showAddress: {type: Boolean, default: true},
      showDistance: {type: Boolean, default: true},
      showTime: {type: Boolean, default: true},
      showTelphone: {type: Boolean, default: true},
      showScopeList: {type: Boolean, default: true},
    },
    data() {
      return {
        nameHtml: '',
        addressHtml: '',
        showSpread: false
      };
    },
    watch: {
      name: {
        handler(newValue, oldValue) {
          this.nameHtml = newValue.replace(this.keyWord, `<span style="color:rgba(41,141,254,1)!important; ">${this.keyWord}</span>`);
        },
        immediate: true,
      },
      address:{
        handler(newValue, oldValue) {
          this.addressHtml = newValue.replace(this.keyWord, `<span style="color:rgba(41,141,254,1)!important; ">${this.keyWord}</span>`);
        },
        immediate: true,
      }

    },
    mounted() {
      // let totalHeight = this.$refs.businessHallItem_scope_list.style.height;
      // let height = this.$refs.businessHallItem_scope_list_item[0].style.height;
      // let totalHeight = window.getComputedStyle(this.$refs.businessHallItem_scope_list).height;
      // let height =window.getComputedStyle(this.$refs.businessHallItem_scope_list_item[0]).height;
      // console.log(`${totalHeight}-----${height}`);
    },
    methods: {
      toDetail() {
        console.log(this.lng + '   ' + this.lat);
        if (!this.lng || !this.lat) {
          Toast({
            mask: false,
            message: '暂无该营业厅位置信息',
            duration: 2000
          });
          return;
        }

        let data = {
          name: this.name,
          address: this.address,
          distance: this.distance,
          time: this.time,
          telphone: this.telphone,
          scopeList: this.scopeList,
          lng: this.lng,
          lat: this.lat
        };
        localStorage.setItem('HallData', JSON.stringify(data));

        this.$router.push('/detailPage');
      },
      spread() {

      }
    },
  }
</script>

<style scoped>
    .businessHallItem {
        background: white;
        position: relative;
        /*border-top-left-radius: 0.2rem;*/
        /*border-top-right-radius: 0.2rem;*/
        border-bottom: 1px solid #DEDEDE;

    }

    .businessHallItem_name {
        max-width: 5.5rem;
        font-size: 0.32rem;
        font-family: PingFang-SC-Bold, PingFang-SC;
        font-weight: bold;
        color: #333;
        margin-left: 0.3rem;
        margin-top: 0.3rem;
    }

    .businessHallItem_distance {
        font-size: 0.28rem;
        font-family: PingFangSC-Regular, PingFang SC;
        font-weight: 400;
        color: rgba(102, 102, 102, 1);

        position: absolute;
        top: 0.3rem;
        right: 0.3rem;
    }

    .businessHallItem_address, .businessHallItem_time, .businessHallItem_telphone {
        font-size: 0.28rem;
        font-family: PingFangSC-Regular, PingFang SC;
        font-weight: 400;
        color: rgba(102, 102, 102, 1);
        margin-left: 0.3rem;
        margin-top: 0.1rem;
        max-width: 5.5rem;
    }

    .businessHallItem_scope {
        margin-top: 0.2rem;
        margin-left: 0.3rem;

    }

    .businessHallItem_scope_title {
        font-size: 0.28rem;
        font-family: PingFangSC-Regular, PingFang SC;
        font-weight: 400;
        color: rgba(102, 102, 102, 1);
        vertical-align: top;
    }

    .businessHallItem_scope_list {
        display: inline-block;
        max-width: 4.5rem;
        /*box-sizing: content-box;*/
    }

    .businessHallItem_scope_list_item {
        display: inline-block;
        font-size: 0.22rem;
        font-family: PingFangSC-Regular, PingFang SC;
        font-weight: 400;
        color: rgba(41, 141, 254, 1);
        border: 1px solid rgba(41, 141, 254, 1);
        margin-left: 0.1rem;
        margin-bottom: 0.1rem;
        border-radius: 0.05rem;
        padding: 0.03rem;
        /*box-sizing: content-box;*/
    }

    .businessHallItem_scope_list_item_key {
        border: 1px solid #D5E9FF;
        background: #D5E9FF;
    }

    .businessHallItem_scope_spread {
        display: inline-flex;
        align-items: center;
        padding-left: 0.1rem;

        width: 0.72rem;
        height: 0.36rem;
        background: rgba(248, 248, 248, 1);
        border-radius: 0.04rem;

        font-size: 0.22rem;
        font-family: PingFangSC-Regular, PingFang SC;
        font-weight: 400;
        color: rgba(153, 153, 153, 1);
        vertical-align: top;
    }
</style>