<template>
    <div class="detailPage">
        <div id="mapContainer"/>
        <div class="detailBlock">
            <business-hall-item
                    :name="name"
                    :address="address"
                    :scopeList="scopeList"
                    :time="time"
                    :telphone="telphone"
                    :lng="lng"
                    :lat="lat"/>
            <p style="text-align: center;background: white">
                <button class="btn" @click="goto">查看路线</button>
            </p>
        </div>
    </div>
</template>

<script>
  import BusinessHallItem from "../components/BusinessHallItem";
  import {Toast} from "vant";

  export default {
    name: "DetailPage",
    components: {BusinessHallItem},
    data() {
      return {
        name: '',
        address: '',
        distance: '',
        time: '',
        telphone: '',
        scopeList: [],
        lng: 0,
        lat: 0
      };

    },
    mounted() {
      let hallData = JSON.parse(localStorage.getItem('HallData'));
      console.log(hallData);
      this.name = hallData.name;
      this.address = hallData.address;
      this.distance = hallData.distance;
      this.time = hallData.time;
      this.telphone = hallData.telphone;
      this.scopeList = hallData.scopeList;
      this.lng = hallData.lng;
      this.lat = hallData.lat;

      AMap.convertFrom([this.lng, this.lat], 'baidu', (status, result) => {
        if (result.info === 'ok') {
          let lnglats = result.locations[0]; // Array.<LngLat>
          this.lat = lnglats.lat;
          this.lng = lnglats.lng;
          console.log(`定位：${this.lng}  ${this.lat}`);
          this.$MapManager.setMapToTarget('mapContainer', this.lng, this.lat);
        }

      });

    },
    methods: {
      goto() {
        Toast.loading({
          mask: false,
          message: '加载中...',
          duration: 0
        });

        this.$MapManager.requestCurrentPosition()
            .then(currentLocation => {
              Toast.clear();
              let from = `${currentLocation.lng},${currentLocation.lat},当前位置`;
              // alert('from:'+from);
              let to = `${this.lng}, ${this.lat},${this.name}`;
              // alert('to:'+to);
              let targeturl = `https://uri.amap.com/navigation?from=${from}&to=${to}`;
              // let targeturl = 'https://uri.amap.com/navigation?from=116.478346,39.997361,startpoint&to=116.3246,39.966577,endpoint&via=116.402796,39.936915,midwaypoint&mode=car&policy=1&src=mypage&coordinate=gaode&callnative=0';
              window.location.href = targeturl;
            })
            .catch(e => {

            });
      }
    }
  }
</script>

<style scoped>
    .detailPage {
        height: 100VH;
        font-size: 10px;
        position: relative;
        font-size: 0;
        display: flex;
        flex-direction: column;
    }

    #mapContainer {
        width: 100VW;
        flex-grow: 1;
    }

    .detailBlock {
        background: white;
        box-shadow: 0rem 0rem 0.18rem 0rem rgba(0, 0, 0, 0.12);
        /*border-radius:0.2rem 0.2rem 0rem 0rem;*/
        border-top-left-radius: 0.2rem;
        border-top-right-radius: 0.2rem;
        padding-top: 0.2rem;
    }

    .btn {
        width: 5.5rem;
        height: 0.88rem;
        background: linear-gradient(118deg, rgba(0, 180, 255, 1) 0%, rgba(21, 159, 255, 1) 100%);
        border-radius: 0.44rem;
        border: 0;
        outline: none;

        font-size: 0.32rem;
        font-family: PingFangSC-Regular, PingFang SC;
        font-weight: 400;
        color: rgba(255, 255, 255, 1);
        margin: 0.2rem;
    }
</style>