import Http from './Http'
import CommonUtils from "./CommonUtils";

function redirect() {
  let target = window.location.origin + window.location.pathname + '?';
  const paramArray = window.location.search.substr(1).split("&");
  for (let i = 0; i < paramArray.length; i++) {
    const param = paramArray[i];
    if (!param.startsWith("wmhToken")) {
      if (i == 0) {
        target = target + param;
      } else {
        target = target + "&" + param;
      }
    }
  }
  window.location.href = "http://wx.10086.cn/website/bind/bindAccount/new?redirectSource=SSO_SG&redirectUrl=" + encodeURIComponent(target);
}

localStorage.setItem('PHONE_KEY', '');
localStorage.setItem('PROVINCE_KEY', '');

const USERINFO_KEY = 'USERINFO_KEY';

let userInfo = {
  userPhone: '',
  provinceCode: '',
  provinceName: '',
  cityCode: '',
  cityName: '',
};

let asyncCallbackArray = [];


function asyncCallback() {
  if (asyncCallbackArray.length > 0) {
    for (let action of asyncCallbackArray) {
      action(userInfo);
    }
    asyncCallbackArray.splice(0, asyncCallbackArray.length);
  }
}

function setUserInfoFromCache() {
  try {
    let cacheUserInfo = JSON.parse(CommonUtils.decrypt(localStorage.getItem(USERINFO_KEY)));
    if (cacheUserInfo && cacheUserInfo.userPhone) {
      userInfo.userPhone = cacheUserInfo.userPhone;
      userInfo.provinceCode = cacheUserInfo.provinceCode;
      userInfo.provinceName = cacheUserInfo.provinceName;
      userInfo.cityCode = cacheUserInfo.cityCode;
      userInfo.cityName = cacheUserInfo.cityName;
    }
  } catch (e) {
  }
}

export default {
  getUserInfo() {
    return userInfo;
  },

  getUserInfoAsync(action) {
    if (userInfo.userPhone) {
      action(userInfo);
    } else {
      asyncCallbackArray.push(action);
    }
  },
  async login() {
    let token = CommonUtils.getUrlParam('wmhToken');
    if (token) {
      let response = await Http.post('http://safeapp.cmoshlw.cn:20011/api/microportalLogin', {
        "apiKey": 'ee721c4d2abe8a59a687e16475b061fb',
        "userName": 'safeapp',
        "token": decodeURIComponent(token)
      });
      if (response.Returncode == 200) {
        userInfo.userPhone = response.ReturnData.telephone;
        try {
          let numUserInfoQuery = await Http.post('http://safeapp.cmoshlw.cn:20011/api/numUserInfoQuery', {
            'apiKey': 'ee721c4d2abe8a59a687e16475b061fb',
            'userName': 'safeapp',
            'userMobile': CommonUtils.encrypt(userInfo.userPhone),
            'check': 2
          });
          if (numUserInfoQuery.Returncode == 200) {
            userInfo.provinceCode = numUserInfoQuery.provCode;
            userInfo.provinceName = numUserInfoQuery.provName;
            userInfo.cityCode = numUserInfoQuery.cityCode;
            userInfo.cityName = numUserInfoQuery.cityName;
            localStorage.setItem(USERINFO_KEY, CommonUtils.encrypt(JSON.stringify(userInfo)));
          }
        } catch (e) {
        }
        asyncCallback();
      } else if (response.Returncode == 401) {//token校验失败
        setUserInfoFromCache();
        if (!userInfo.userPhone) {
          redirect();
        }else {
          asyncCallback();
        }
      }
    } else {
      //没有传入token，重定向到登录页面
      redirect();
    }
  }
}
