import CommonUtils from "./CommonUtils";

const currentHost = window.location.hostname;

const productHost = ['wx.10086.cn', 'wx.online-cmcc.cn','security.online-cmos.cn'];

const testUrl = 'http://221.181.129.89:20120/api/';
const aliHttpsUrl = 'https://www.zyfjqq.online/api/';
const grayUrl = 'http://117.159.206.253:20011/gray/api/';
let productUrl = '';
if (CommonUtils.getUrlParam('protocol') == 1) {
  productUrl = 'https://security.online-cmos.cn/safeapp/api/';
} else {
  productUrl = 'http://safeapp.cmoshlw.cn:20011/api/';
}

const isInProduct = productHost.includes(currentHost);

export function url(env) {
  if (isInProduct) {
    return productUrl;
  }

  let url = '';

  switch (env) {
    case 'prod':
      url = productUrl;
      break;
    case 'test':
      url = testUrl;
      break;
    case 'gray':
      url = grayUrl;
      break;
    case 'ali':
      url = aliHttpsUrl;
      break;
    default:
      url = productUrl;
  }
  return url;
}