import Http from './Http'
import {url} from './UrlManager'
import CommonUtils from "./CommonUtils";

const UUID_KEY = 'HLWXXAQ_UUID_KEY';

function getUUID() {
  let uuid = localStorage.getItem(UUID_KEY);
  if (!uuid) {
    //生成uuid
    uuid = generateUUID(32);
    localStorage.setItem(UUID_KEY, uuid);
  }

  return uuid;
}

function generateUUID(length) {
  var str = Math.random().toString(36).substr(2);
  if (str.length >= length) {
    return str.substr(0, length);
  }
  str += generateUUID(length - str.length);
  return str;
}

let url_prex =url('test');

export default {
  //接口详情见 https://shimo.im/docs/8cYJrTwtkWcc68pc
  logRecord(businessType, acceptNumber, subNumber,
      channel, businessName, status,
      effectiveTime, flag) {
    Http.post(url_prex + "logRecord",
        {
          'apiKey': 'ee721c4d2abe8a59a687e16475b061fb',
          'userName': 'safeapp',
          businessType: businessType,
          acceptNumber: CommonUtils.encrypt(acceptNumber),
          subNumber: CommonUtils.encrypt(subNumber),
          channel: channel,
          businessName: businessName,
          status: status,
          effectiveTime: effectiveTime,
          flag: flag,
          uuid: getUUID()
        })
        .then((response) => {
        })
        .catch(() => {
        });
  }
}