<template>
    <div>
        <div class="searchBar">
            <div class="inputBlock">
                <img class="inputBlock__img" src="../assets/img/icon_search.png">
                <cube-input v-model="inputValue"
                            @input="handleInput"
                            :autofocus="false"
                            @focus="onFocus"
                            placeholder="搜索营业厅或要办理的业务"/>
                <img class="inputBlock__clear" src="../assets/img/icon_clear.png" @click="clear">
            </div>
            <span class="cancel" v-show="showCancel" @click="cancel">取消</span>
        </div>
        <overlay :show="showOverlay" @click="overlayClick">
        </overlay>
    </div>

</template>

<script>
  import {Input} from 'cube-ui'
  import lodash from 'lodash'
  import {Overlay} from 'vant';

  export default {
    name: "SearchBar",
    components: {CubeInput: Input, Overlay},
    data() {
      return {
        inputValue: '',
        showCancel: false,
        showOverlay: false
      }
    },
    methods: {
      handleInput: lodash.debounce(function () {
        this.$emit('search-bar-input', this.inputValue);
        this.showOverlay = false;
        // this.showCancel = false;
      }, 500),
      clear() {
        this.inputValue = '';
        this.$emit('search-bar-input', this.inputValue);
      },
      cancel() {
        this.inputValue = '';
        this.$emit('search-bar-cancel', this.inputValue);
        this.showOverlay = false;
        this.showCancel = false;
      },
      onFocus() {
        if (!this.inputValue) {
          this.showOverlay = true;
          this.showCancel = true;
        }
      },
      overlayClick() {
        console.log('overlayClick');
        this.showOverlay = false;
      }
    }
  }
</script>

<style scoped>
    .searchBar {
        position: relative;
        height: 1.16rem;
        background-color: rgba(238, 238, 238, 1);
        font-size: 0;
        display: flex;
        justify-content: space-between;
        align-items: center;
        border-bottom: 1px solid #EEEEEE;

        z-index: 201;
    }

    .inputBlock {
        margin-left: 0.3rem;
        margin-right: 0.3rem;
        height: 0.76rem;
        background: white;
        border-radius: 0.1rem;
        /*opacity: 0.5;*/
        flex: 1;

        display: flex;
        align-items: center;


    }

    .inputBlock__img {
        width: 0.28rem;
        height: 0.28rem;
        margin-left: 0.24rem;
        margin-right: 0.24rem;
        /*vertical-align: middle;*/
    }

    .inputBlock__clear {
        width: 0.32rem;
        height: 0.32rem;
        margin-left: 0.24rem;
        margin-right: 0.24rem;
        /*vertical-align: middle;*/
    }

    .cancel {
        width: 0.8rem;
        display: inline-block;
        font-size: 0.28rem;
        font-family: PingFang-SC;
        font-weight: 500;
        color: #298DFE;
        line-height: 1.16rem;
        vertical-align: top;
    }
</style>

<style>
    .cube-input {
        background-color: transparent;
        flex: 1;
    }

    .cube-input_active:after {
        border: 0;
    }

    .cube-input-field {
        border: 0;
        background-color: transparent;

    }
</style>
