<template>
    <div class="home">
        <search-bar @search-bar-input="handleSearchBarInput"
                    @search-bar-cancel="handleSearchBarCancel"/>
        <list v-model="loading"
              :finished="finished"
              :immediate-check="false"
              :offset="50"
              finished-text="没有更多了"
              @load="onLoad"
              v-if="!loadStatus">
            <business-hall-item
                    v-for="(item,index) in businessHallListData"
                    :key="index"
                    :key-word="searchKeyWord"
                    :name="item.knowledgeName"
                    :address="item.detailAddress"
                    :distance="item.pointDistance"
                    :scopeList="item.specialService"
                    :telphone="item.phone"
                    :show-telphone="false"
                    :time="item.businessTime"
                    :show-time="false"
                    :lng="item.pointInfo.lon"
                    :lat="item.pointInfo.lat"/>
        </list>

        <load-status-block :status="loadStatus" v-if="loadStatus"/>

    </div>
</template>

<script>
  import SearchBar from "../components/SearchBar";
  import BusinessHallItem from "../components/BusinessHallItem";
  import {List, Toast} from 'vant';
  import LoadStatusBlock from "../components/LoadStatusBlock";

  export default {
    name: 'home',
    components: {LoadStatusBlock, BusinessHallItem, SearchBar, List, Toast},
    data() {
      return {
        locationInfo: '',
        businessHallListData: [],
        loading: false,
        finished: false,
        currentPage: 0,
        searchKeyWord: '',
        currentLocation: {},
        loadStatus: ''
      };
    },
    methods: {
      init() {
        this.currentPage = 0;
        this.businessHallListData.splice(0, this.businessHallListData.length);
        this.finished = false;
        this.loadStatus = '';
      },
      handleSearchBarInput(input) {
        this.searchKeyWord = input;
        console.log('handleSearchBarInput:' + this.searchKeyWord);
        this.init();
        this.onLoad();
        // if (this.searchKeyWord) {
        //
        // } else {
        //
        // }

      },
      handleSearchBarCancel() {
        console.log('handleSearchBarCancel');
      },
      onLoad() {
        console.log('onload--' + this.searchKeyWord);
        this.loading = true;
        if (this.searchKeyWord) {
          this.onLoadBySearch();
        } else {
          this.onLoadByLocation();
        }
      },
      onLoadByLocation() {
        console.log('onLoadByLocation');
        let targetPage = this.currentPage + 1;
        this.$DataManager.getNearbyBusinessHall(
            this.currentLocation.lng,
            this.currentLocation.lat,
            this.currentLocation.cityName,
            targetPage)
            .then((data) => {
              Toast.clear();
              this.currentPage = this.currentPage + 1;

              let {total, list} = data;
              console.log(total);
              console.log(list);
              if (this.currentPage == 1) {//请求的是第一页的话，清空旧数据
                this.businessHallListData.splice(0, this.businessHallListData.length);
              }
              for (let item of list) {
                this.businessHallListData.push(item);
              }

              this.loading = false;
              this.finished = (this.businessHallListData.length >= total);
            })
            .catch(e => {
              if (e == '无结果') {
                this.loading = false;
                this.finished = true;
                this.loadStatus = 'null';
              }
            });
      },
      onLoadBySearch() {
        console.log('onLoadBySearch');
        let targetPage = this.currentPage + 1;
        this.$DataManager.searchBusinessHallByKeyword(
            this.currentLocation.lng,
            this.currentLocation.lat,
            this.searchKeyWord,
            this.currentLocation.cityName,
            targetPage)
            .then((data) => {
              Toast.clear();
              this.currentPage = this.currentPage + 1;

              let {total, list} = data;
              console.log(total);
              console.log(list);
              console.log('this.currentPage:' + this.currentPage);
              if (this.currentPage == 1) {//请求的是第一页的话，清空旧数据
                this.businessHallListData.splice(0, this.businessHallListData.length);
              }
              for (let item of list) {
                this.businessHallListData.push(item);
              }

              this.loading = false;
              this.finished = (this.businessHallListData.length >= total);
            })
            .catch((e) => {
              if (e == '无搜索结果') {
                this.loading = false;
                this.finished = true;
                this.loadStatus = 'searchNull';
              }
            });
      }
    },
    mounted() {
      Toast.loading({
        mask: false,
        message: '定位中...',
        duration: 0
      });
      this.$MapManager.requestCurrentPosition()
          .then(currentLocation => {
            Toast.clear();
            console.log(currentLocation);
            this.currentLocation = currentLocation;
            this.onLoad();
          })
          .catch(e => {
            if (e == '当前无法获取定位') {
              Toast.fail('当前无法获取定位');
              this.loadStatus = 'null';
            }
          });
    }
  }
</script>

