import Vue from 'vue'
// import './cube-ui'
import App from './App.vue'
import router from './router'
import './assets/css/common.css'
import MapManager from "./js/MapManager";
import DataManager from "./js/DataManager";

Vue.config.productionTip = false
Vue.prototype.$MapManager = MapManager;
Vue.prototype.$DataManager = DataManager;

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')

MapManager.requestCurrentPosition()
    .then(data=>{})
    .catch(e=>{});